.BriefcaseMain {
    padding-top: 1.5rem !important;
    padding-bottom: 1.5rem !important;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    &__item {
        margin: 2.5rem auto;
        width: 100%;
        max-width: 500px;
        padding: 0 1rem;
        &__figure {
            width: 100%;
            &__img {
                width: 100%;
            }
            &__figcaption {
                margin: 0.5rem 0;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                &__title {
                    color: $secondaryColor;
                }
                &__title,
                p,
                a {
                    margin-bottom: .3rem;
                    display: inline-block;
                }
                p,
                a {
                    color: #FFFFFF;
                    font-size: 0.9rem;
                    text-align: center;
                }
                a {
                    text-decoration: none;
                    margin-bottom: 0rem;
                    &:hover {
                        text-shadow: 0 0 10px #FFFFFF;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 575px) {}

@media screen and (min-width: 576px) {}

@media screen and (min-width: 768px) {}

@media screen and (min-width: 992px) {
    .BriefcaseMain {
        flex-direction: row;
        flex-wrap: wrap;
        &__item {
            width: 45%;
            max-width: 500px;
        }
    }
}

