.ContactForm {
    display: grid;
    grid-template-columns: 1fr;
    width: 100%;
    padding-top: 2rem;
    padding-bottom: 2rem;
    .hiddenMessage {
        opacity: 0 !important;
    }
    .showMessage {
        opacity: 1;
    }
    &__containForm {
        width: 100%;
        padding: 1rem;
        &__form {
            width: 100%;
            display: grid;
            grid-template-columns: 1fr;
            text-align: center;
            &__col01,
            &__col02,
            &__col03 {
                text-align: center;
                padding: 0;
            }
            &__col01 {
                .inputGroup {
                    position: relative;
                    height: 85px;
                    .errorMessage {
                        color: #FF0000;
                        font-size: 0.8rem;
                        text-align: left;
                        padding-left: 0.6rem;
                    }
                }
            }
            &__col03 {
                margin-top: 1.5rem;
                margin-bottom: 1.5rem;
                position: relative;
                .successMessageForm,
                .errorMessageForm {
                    position: absolute;
                    top: -3.5rem;
                    padding: .5rem;
                    border: none;
                    transition: all 0.5s linear;
                }
                .errorMessageForm {
                    background-color: rgba(255, 0, 0, 0.5);
                    b {
                        font-weight: bold;
                    }
                }
                .successMessageForm {
                    background-color: rgba(23, 153, 23, 0.5);
                }
            }
            .formInputs {
                * {
                    color: #FFFFFF !important;
                }
            }
            .MuiButtonBase-root {
                min-width: 170px;
            }
        }
    }
    &__containData {
        padding: 1rem 1.8rem;
        &__list {
            background-color: $thirdColor;
            padding: 2rem 1rem;
            max-width: 300px;
            margin: auto;
            border: none;
            border-radius: 10px;
            &__item {
                width: 100%;
                a {
                    color: #FFFFFF;
                    text-decoration: none;
                    display: flex;
                    align-items: center;
                    .MuiSvgIcon-root {
                        font-size: 3rem;
                    }
                    p {
                        margin-left: .5rem;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 575px) {}

@media screen and (min-width: 576px) {
    .ContactForm {
        &__containForm {
            &__form {
                &__col01,
                &__col02,
                &__col03 {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                }
            }
        }
    }
}

@media screen and (min-width: 768px) {
    .ContactForm {
        padding-top: 3rem !important;
        padding-bottom: 3rem !important;
        &__containForm {
            &__form {
                grid-template-columns: 1fr 1fr;
                min-width: 660px !important;
                gap: 1rem;
                &__col01,
                &__col02,
                &__col03 {
                    justify-content: start;
                }
                &__col01 {
                    align-items: flex-end;
                }
                &__col02 {
                    align-items: flex-start;
                    .MuiInputBase-multiline {
                        height: 225px;
                    }
                }
                &__col03 {
                    grid-column: span 2;
                    margin-top: 0;
                    .successMessageForm,
                .errorMessageForm {
                    top: -2.1rem;
                }
                }
            }
        }
    }
}

@media screen and (min-width: 992px) {}

@media screen and (min-width: 1200px) {
    .ContactForm {
        grid-template-columns: 62% 38%;
        gap: 1rem;
        &__containData {
            width: 80%;
            margin-top: 0.5rem;
            margin-left: auto;
            &__list {
                padding: 0.8rem 1rem;
                &__item {
                    a {
                        flex-direction: column;
                        p {
                            margin-top: 0.5rem;
                            margin-bottom: 1rem;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 1400px) {
    .ContactForm {
        grid-template-columns: 58% 42%;
        &__containData {
            width: 88%;
            &__list {
                &__item {
                    a {
                        flex-direction: row;
                        margin: 1rem auto;
                    }
                }
            }
        }
    }
}