// Karantina and Roboto Fonts
@import url('https://fonts.googleapis.com/css2?family=Karantina:wght@300;400;700&family=Roboto:wght@100;300;400;500;700;900&display=swap');
*,
body {
    font-family: 'Roboto', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    display: block;
    width: 100%;
    text-align: center;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3rem;
    color: white;
    margin: 2rem auto;
}

h3 {
    font-size: 2rem;
    line-height: 2rem;
    color: $secondaryColor;
    font-weight: bold;
    margin: 0;
}

h4 {
    font-size: 2rem;
    color: #FFFFFF;
}

h5 {
    font-size: 1.5rem;
}

h6 {
    font-size: 1.2rem;
}