.Header {
    position: fixed;
    background: rgb(40, 44, 52);
    background: linear-gradient(180deg, rgba(40, 44, 52, 1) 0%, rgba(40, 44, 52, 1) 50%, rgba(32, 35, 42, 1) 100%);
    box-shadow: -1px 10px 5px 0px rgba(0, 0, 0, 0.75);
    padding: 0.7rem 0.5rem;
    &__logo {
        padding: 0;
        &__link {
            text-decoration: none;
            &__title {
                font-family: 'Karantina';
                font-weight: 300;
                font-size: 3rem;
                color: #FFFFFF;
                span {
                    font-weight: 700;
                }
            }
        }
    }
    &__nav {
        width: 100%;
        text-align: center;
        margin: 0 auto;
        * {
            text-decoration: none !important;
        }
        &__link {
            margin: 0 0.1rem !important;
            a {
                color: $secondaryColor;
                .MuiButton-label {
                    text-decoration: none !important;
                    border: none !important;
                    border: 0;
                }
            }
        }
    }
}

@media screen and (max-width: 575px) {
    .Header {
        &__logo {
            display: none;
        }
    }
}

@media screen and (min-width: 576px) {
    .Header {
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding: 0 0.3rem;
        &__nav {
            width: auto;
            margin-right: 0;
            margin-left: auto;
            display: flex;
            &__link {
                margin-left: 0.6rem !important;
            }
        }
    }
}

