.knowledge {
    min-height: 100vh;
    width: 100%;
    padding-bottom: 3rem !important;
    &__list {
        display: flex;
        &__item {
            width: 80%;
            min-width: 150px;
            max-width: 200px;
            margin: 2rem 1rem;
            text-align: center;
            border: none;
            padding: 0;
            border: 2px solid $secondaryColor;
            border-radius: 10px;
            &__picture {
                width: 70%;
                margin: 0 auto;
                img {
                    width: 100%;
                    margin: 2rem auto;
                }
                figcaption {
                    display: none;
                }
            }
            &__title {
                border-top: 2px solid $secondaryColor;
                line-height: 4rem;
            }
        }
    }
}

@media screen and (max-width: 575px) {
    .knowledge {
        &__list {
            flex-direction: column;
            align-items: center;
            flex-wrap: nowrap;
        }
    }
}

@media screen and (min-width: 576px) {
    .knowledge {
        &__list {
            flex-wrap: wrap;
            justify-content: space-around;
            &__item {
                position: relative;
                overflow: hidden;
            }
        }
    }
}

@media screen and (min-width: 768px) {
    .knowledge {
        &__list {
            justify-content: space-between;
        }
    }
}

