@import url("https://fonts.googleapis.com/css2?family=Karantina:wght@300;400;700&family=Roboto:wght@100;300;400;500;700;900&display=swap");
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

*,
body {
  font-family: "Roboto", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  display: block;
  width: 100%;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
  color: white;
  margin: 2rem auto;
}

h3 {
  font-size: 2rem;
  line-height: 2rem;
  color: #f50057;
  font-weight: bold;
  margin: 0;
}

h4 {
  font-size: 2rem;
  color: #FFFFFF;
}

h5 {
  font-size: 1.5rem;
}

h6 {
  font-size: 1.2rem;
}

header {
  z-index: 100;
}

header,
main,
section,
footer {
  width: 100%;
  margin: 0 auto !important;
}

@media screen and (min-width: 768px) {
  header,
main,
section,
footer {
    padding: 0 3%;
  }
}
@media screen and (min-width: 992px) {
  header,
main,
section,
footer {
    padding: 0 5% !important;
  }
}
@media screen and (min-width: 1200px) {
  header,
main,
section,
footer {
    padding: 0 10% !important;
  }
}
@media screen and (min-width: 1400px) {
  header,
main,
section,
footer {
    padding: 0 15% !important;
  }
}
/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
  .animate__animated, .animated {
    /*CSS transitions*/
    -o-transition-property: none !important;
    -moz-transition-property: none !important;
    -ms-transition-property: none !important;
    -webkit-transition-property: none !important;
    transition-property: none !important;
    /*CSS transforms*/
    -o-transform: none !important;
    -moz-transform: none !important;
    -ms-transform: none !important;
    -webkit-transform: none !important;
    transform: none !important;
    /*CSS animations*/
    -webkit-animation: none !important;
    -moz-animation: none !important;
    -o-animation: none !important;
    -ms-animation: none !important;
    animation: none !important;
  }
}
body {
  background-color: #282c34;
}

.Header {
  position: fixed;
  background: #282c34;
  background: linear-gradient(180deg, #282c34 0%, #282c34 50%, #20232a 100%);
  box-shadow: -1px 10px 5px 0px rgba(0, 0, 0, 0.75);
  padding: 0.7rem 0.5rem;
}
.Header__logo {
  padding: 0;
}
.Header__logo__link {
  text-decoration: none;
}
.Header__logo__link__title {
  font-family: "Karantina";
  font-weight: 300;
  font-size: 3rem;
  color: #FFFFFF;
}
.Header__logo__link__title span {
  font-weight: 700;
}
.Header__nav {
  width: 100%;
  text-align: center;
  margin: 0 auto;
}
.Header__nav * {
  text-decoration: none !important;
}
.Header__nav__link {
  margin: 0 0.1rem !important;
}
.Header__nav__link a {
  color: #f50057;
}
.Header__nav__link a .MuiButton-label {
  text-decoration: none !important;
  border: none !important;
  border: 0;
}

@media screen and (max-width: 575px) {
  .Header__logo {
    display: none;
  }
}
@media screen and (min-width: 576px) {
  .Header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0 0.3rem;
  }
  .Header__nav {
    width: auto;
    margin-right: 0;
    margin-left: auto;
    display: flex;
  }
  .Header__nav__link {
    margin-left: 0.6rem !important;
  }
}
.Footer {
  background-color: #20232a;
  overflow: hidden;
}
.Footer a {
  color: #FFFFFF;
  text-decoration: none;
}
.Footer__social {
  width: 300px;
  margin: 3rem auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
  transition: all 0.2s ease-in-out;
}
.Footer__social a i {
  font-size: 4rem;
}
.Footer__social a span {
  display: none;
}
.Footer__social a:hover {
  text-shadow: 0px 2px 8px rgba(255, 255, 255, 0.52);
}
.Footer__menu {
  width: 300px;
  margin: 2rem auto;
}
.Footer__menu__list {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.Footer__developed {
  margin-top: auto;
  width: 100%;
  text-align: center;
}
.Footer__developed p {
  line-height: 3rem;
  color: #FFFFFF;
}

.CoverPage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-image: url("./assets/images/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.CoverPage__contain__title, .CoverPage__contain__subtitle {
  color: #000000;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.8);
  text-shadow: 3px 2px 3px rgba(255, 255, 255, 0.2);
}
.CoverPage__contain__title {
  font-size: 3rem;
  margin-bottom: 0rem;
}
.CoverPage__contain__subtitle {
  margin-top: 0.3rem;
  font-size: 1.6rem;
}

@media screen and (min-width: 576px) {
  .CoverPage__contain__title {
    font-size: 3.5rem;
  }
  .CoverPage__contain__subtitle {
    font-size: 1.95rem;
  }
}
@media screen and (min-width: 768px) {
  .CoverPage {
    background-position: bottom;
  }
  .CoverPage__contain__title {
    font-size: 4rem;
  }
  .CoverPage__contain__subtitle {
    font-size: 2.2rem;
  }
}
@media screen and (min-width: 992px) {
  .CoverPage {
    background-position-y: -50px;
  }
  .CoverPage__contain__title {
    font-size: 4.5rem;
  }
  .CoverPage__contain__subtitle {
    font-size: 2.5rem;
  }
}
@media screen and (min-width: 1200px) {
  .CoverPage {
    background-position-y: -200px;
  }
  .CoverPage__contain__title {
    font-size: 5rem;
  }
  .CoverPage__contain__subtitle {
    font-size: 2.75rem;
  }
}
@media screen and (min-width: 1400px) {
  .CoverPage {
    background-position-y: -300px;
  }
}
.Whoami {
  min-height: 100vh !important;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Whoami__content {
  padding: 2rem;
}

@media screen and (min-width: 768px) {
  .Whoami__content {
    padding: 0 3rem;
  }
  .Whoami__content__text {
    border: 1px double #f50057;
    border-radius: 10px;
    padding: 3rem;
  }
}
@media screen and (min-width: 992px) {
  .Whoami__content {
    padding: 0rem 4rem;
  }
  .Whoami__content__text {
    padding: 6rem 4rem;
  }
}
@media screen and (min-width: 1200px) {
  .Whoami__content {
    padding: 0 5rem;
  }
  .Whoami__content__text {
    padding: 7rem 4rem;
  }
}
.knowledge {
  min-height: 100vh;
  width: 100%;
  padding-bottom: 3rem !important;
}
.knowledge__list {
  display: flex;
}
.knowledge__list__item {
  width: 80%;
  min-width: 150px;
  max-width: 200px;
  margin: 2rem 1rem;
  text-align: center;
  border: none;
  padding: 0;
  border: 2px solid #f50057;
  border-radius: 10px;
}
.knowledge__list__item__picture {
  width: 70%;
  margin: 0 auto;
}
.knowledge__list__item__picture img {
  width: 100%;
  margin: 2rem auto;
}
.knowledge__list__item__picture figcaption {
  display: none;
}
.knowledge__list__item__title {
  border-top: 2px solid #f50057;
  line-height: 4rem;
}

@media screen and (max-width: 575px) {
  .knowledge__list {
    flex-direction: column;
    align-items: center;
    flex-wrap: nowrap;
  }
}
@media screen and (min-width: 576px) {
  .knowledge__list {
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .knowledge__list__item {
    position: relative;
    overflow: hidden;
  }
}
@media screen and (min-width: 768px) {
  .knowledge__list {
    justify-content: space-between;
  }
}
.briefcaseCoverpage {
  height: 100vh;
  width: 100%;
  background-image: url("./assets/images/briefcaseHeader.jpg");
  background-position: left;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.briefcaseCoverpage__title {
  color: #FFFFFF;
  text-shadow: #474747 3px 5px 2px;
}

@media screen and (min-width: 768px) {
  .briefcaseCoverpage {
    background-position: center;
  }
}
.BriefcaseMain {
  padding-top: 1.5rem !important;
  padding-bottom: 1.5rem !important;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.BriefcaseMain__item {
  margin: 2.5rem auto;
  width: 100%;
  max-width: 500px;
  padding: 0 1rem;
}
.BriefcaseMain__item__figure {
  width: 100%;
}
.BriefcaseMain__item__figure__img {
  width: 100%;
}
.BriefcaseMain__item__figure__figcaption {
  margin: 0.5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.BriefcaseMain__item__figure__figcaption__title {
  color: #f50057;
}
.BriefcaseMain__item__figure__figcaption__title,
.BriefcaseMain__item__figure__figcaption p,
.BriefcaseMain__item__figure__figcaption a {
  margin-bottom: 0.3rem;
  display: inline-block;
}
.BriefcaseMain__item__figure__figcaption p,
.BriefcaseMain__item__figure__figcaption a {
  color: #FFFFFF;
  font-size: 0.9rem;
  text-align: center;
}
.BriefcaseMain__item__figure__figcaption a {
  text-decoration: none;
  margin-bottom: 0rem;
}
.BriefcaseMain__item__figure__figcaption a:hover {
  text-shadow: 0 0 10px #FFFFFF;
}

@media screen and (min-width: 992px) {
  .BriefcaseMain {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .BriefcaseMain__item {
    width: 45%;
    max-width: 500px;
  }
}
.ContactCoverpage {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("./assets/images/contactHeader01.jpeg");
  background-position: right bottom;
  background-repeat: no-repeat;
  background-size: cover;
}
.ContactCoverpage__title {
  color: #FFFFFF;
  text-shadow: #474747 3px 5px 2px;
}

.ContactForm {
  display: grid;
  grid-template-columns: 1fr;
  width: 100%;
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.ContactForm .hiddenMessage {
  opacity: 0 !important;
}
.ContactForm .showMessage {
  opacity: 1;
}
.ContactForm__containForm {
  width: 100%;
  padding: 1rem;
}
.ContactForm__containForm__form {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  text-align: center;
}
.ContactForm__containForm__form__col01, .ContactForm__containForm__form__col02, .ContactForm__containForm__form__col03 {
  text-align: center;
  padding: 0;
}
.ContactForm__containForm__form__col01 .inputGroup {
  position: relative;
  height: 85px;
}
.ContactForm__containForm__form__col01 .inputGroup .errorMessage {
  color: #FF0000;
  font-size: 0.8rem;
  text-align: left;
  padding-left: 0.6rem;
}
.ContactForm__containForm__form__col03 {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  position: relative;
}
.ContactForm__containForm__form__col03 .successMessageForm,
.ContactForm__containForm__form__col03 .errorMessageForm {
  position: absolute;
  top: -3.5rem;
  padding: 0.5rem;
  border: none;
  transition: all 0.5s linear;
}
.ContactForm__containForm__form__col03 .errorMessageForm {
  background-color: rgba(255, 0, 0, 0.5);
}
.ContactForm__containForm__form__col03 .errorMessageForm b {
  font-weight: bold;
}
.ContactForm__containForm__form__col03 .successMessageForm {
  background-color: rgba(23, 153, 23, 0.5);
}
.ContactForm__containForm__form .formInputs * {
  color: #FFFFFF !important;
}
.ContactForm__containForm__form .MuiButtonBase-root {
  min-width: 170px;
}
.ContactForm__containData {
  padding: 1rem 1.8rem;
}
.ContactForm__containData__list {
  background-color: #20232a;
  padding: 2rem 1rem;
  max-width: 300px;
  margin: auto;
  border: none;
  border-radius: 10px;
}
.ContactForm__containData__list__item {
  width: 100%;
}
.ContactForm__containData__list__item a {
  color: #FFFFFF;
  text-decoration: none;
  display: flex;
  align-items: center;
}
.ContactForm__containData__list__item a .MuiSvgIcon-root {
  font-size: 3rem;
}
.ContactForm__containData__list__item a p {
  margin-left: 0.5rem;
}

@media screen and (min-width: 576px) {
  .ContactForm__containForm__form__col01, .ContactForm__containForm__form__col02, .ContactForm__containForm__form__col03 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
@media screen and (min-width: 768px) {
  .ContactForm {
    padding-top: 3rem !important;
    padding-bottom: 3rem !important;
  }
  .ContactForm__containForm__form {
    grid-template-columns: 1fr 1fr;
    min-width: 660px !important;
    gap: 1rem;
  }
  .ContactForm__containForm__form__col01, .ContactForm__containForm__form__col02, .ContactForm__containForm__form__col03 {
    justify-content: start;
  }
  .ContactForm__containForm__form__col01 {
    align-items: flex-end;
  }
  .ContactForm__containForm__form__col02 {
    align-items: flex-start;
  }
  .ContactForm__containForm__form__col02 .MuiInputBase-multiline {
    height: 225px;
  }
  .ContactForm__containForm__form__col03 {
    grid-column: span 2;
    margin-top: 0;
  }
  .ContactForm__containForm__form__col03 .successMessageForm,
.ContactForm__containForm__form__col03 .errorMessageForm {
    top: -2.1rem;
  }
}
@media screen and (min-width: 1200px) {
  .ContactForm {
    grid-template-columns: 62% 38%;
    gap: 1rem;
  }
  .ContactForm__containData {
    width: 80%;
    margin-top: 0.5rem;
    margin-left: auto;
  }
  .ContactForm__containData__list {
    padding: 0.8rem 1rem;
  }
  .ContactForm__containData__list__item a {
    flex-direction: column;
  }
  .ContactForm__containData__list__item a p {
    margin-top: 0.5rem;
    margin-bottom: 1rem;
  }
}
@media screen and (min-width: 1400px) {
  .ContactForm {
    grid-template-columns: 58% 42%;
  }
  .ContactForm__containData {
    width: 88%;
  }
  .ContactForm__containData__list__item a {
    flex-direction: row;
    margin: 1rem auto;
  }
}
.NotFound {
  width: 100%;
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.NotFound__picture, .NotFound__text {
  opacity: 0.5;
}
.NotFound__picture {
  width: 100%;
  text-align: center;
}
.NotFound__picture img {
  width: 100%;
  max-width: 500px;
}
.NotFound__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.NotFound__text h4,
.NotFound__text p {
  margin: 1rem auto;
  padding: 0 1rem;
}
.NotFound__text p {
  text-align: center;
  line-height: 1.2rem;
  color: #FFFFFF;
}

@media screen and (min-width: 768px) {
  .NotFound {
    height: 60vh;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  }
}
@media screen and (min-width: 1200px) {
  .NotFound {
    height: 70vh;
  }
}

