.Footer {
    background-color: $thirdColor;
    overflow: hidden;
    a {
        color: #FFFFFF;
        text-decoration: none;
    }
    &__social {
        width: 300px;
        margin: 3rem auto;
        display: flex;
        align-items: center;
        justify-content: space-around;
        transition: all 0.2s ease-in-out;
        a {
            i {
                font-size: 4rem;
            }
            span {
                display: none;
            }
            &:hover {
                text-shadow: 0px 2px 8px rgba(255, 255, 255, 0.52);
            }
        }
    }
    &__menu {
        width: 300px;
        margin: 2rem auto;
        &__list {
            display: flex;
            align-items: center;
            justify-content: space-around;
        }
    }
    &__developed {
        margin-top: auto;
        width: 100%;
        text-align: center;
        p {
            line-height: 3rem;
            color: #FFFFFF;
        }
    }
}