.NotFound {
    width: 100%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    &__picture,
    &__text {
        opacity: .5;
    }
    &__picture {
        width: 100%;
        text-align: center;
        img {
            width: 100%;
            max-width: 500px;
        }
    }
    &__text {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        h4,
        p {
            margin: 1rem auto;
            padding: 0 1rem;
        }
        p {
            text-align: center;
            line-height: 1.2rem;
            color: #FFFFFF;
        }
    }
}

@media screen and (max-width: 575px) {}

@media screen and (min-width: 576px) {}

@media screen and (min-width: 768px) {
    .NotFound {
        height: 60vh;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;
    }
}

@media screen and (min-width: 992px) {}

@media screen and (min-width: 1200px) {
    .NotFound {
        height: 70vh;
    }
}

@media screen and (min-width: 1400px) {}